:root {
	/* Color Palette */
	--color-white: #fff;
	--color-off-white: #aaa;
  --color-light: #eee;
	--color-light-gray: #999;
	--color-grey: #a3a3a3;
	--color-dark-gray: #373a3e;
	--color-black: #111;
  --color-dark: #4a00cc;
	--color-red: #dc0005;
  --color-deep-orange: #ff2800;
  --color-orange: #ff7a00;
  --color-amber: #ffaf00;
  --color-yellow: #fadc00;
  --color-lime: #dfdc00;
  --color-light-green: #95cc0d;
  --color-green: #13b20b;
  --color-teal: #339985;
  --color-cyan: #00b1cc;
  --color-light-blue: #6da7d1;
  --color-blue: #0d7ecc;
	--color-dark-blue: #3b4eb8;
  --color-indigo: #3b4eb8;
  --color-deep-purple: #639;
  --color-purple: #c30c70;
  --color-pink: #eb2f59;
  --color-brown: #7a5653;
  --color-blue-grey: #617c8f;
	--color-border: rgba(0, 0, 0, 0.1);

	--font-color: #373a3e;

	--font-color: var(--color-dark-gray);
	--font-color-hover: #111;
	--font-color-hover: var(--color-black);
	--font-color-inverse: #aaa;
	--font-color-inverse: var(--color-off-white);
	--font-color-inverse-hover: #fff;
	--font-color-inverse-hover: var(--color-white);
	--link-color: #ff2800;
	--link-color: var(--color-deep-orange);
	--link-color-hover: #eee;
	--link-color-hover: var(--color-light);

	--dark-background-color: #212329;
	--darker-background-color: #141414;

	/* Typography */
	--body-font-size: 15px;

	--block-quote-font-size: 15px;
	--block-quote-font-weight: 500;

	--pre-font-size: 13px;

	--header1-font-size: 25px;
	--header1-line-height: 1.2;

	--header2-font-size: 21px;
	--header2-line-height: 1.3;

	--header3-font-size: 19px;
	--header3-line-height: 1.4;

	--header4-font-size: 19px;
	--header4-line-height: 1.4;

	--header5-font-size: 17px;
	--header5-line-height: 1.4;

	--header6-font-size: 15px;
	--header6-line-height: 1.4;

	--table-first-column-font-size: 15px;

	--feature-points-font-size: 15px;

	/* App Variables */
	--border-radius: 4px;
	--grid-size: 10px;
	--card-padding: calc(10px * 2);
	--card-padding: calc(var(--grid-size) * 2);

	--header-background-color: #212329;

	--header-background-color: var(--dark-background-color);
	--coloresponsive-header-background-color: #141414;
	--coloresponsive-header-background-color: var(--darker-background-color);
	--hero-background: #17191c;
	--menu-offset: calc(10px * 2);
	--menu-offset: calc(var(--grid-size) * 2);
	--toggle-button-width: calc(10px * 2.6);
	--toggle-button-width: calc(var(--grid-size) * 2.6);
	--header-size: calc(10px * 6);
	--header-size: calc(var(--grid-size) * 6);

	--menu-height: calc(10px * 4.4);

	--menu-height: calc(var(--grid-size) * 4.4);

	--content-top-padding: calc(10px * 6);

	--content-top-padding: calc(var(--grid-size) * 6);
	--content-padding: calc(10px * 3);
	--content-padding: calc(var(--grid-size) * 3);
	--console-background-color: #141414;
	--console-background-color: var(--darker-background-color);
}

@media (max-width: 768px) {
	:root {
		--header1-font-size: 24px;
		--header2-font-size: 22px;
		--header3-font-size: 20px;
		--header4-font-size: 18px;
		--header5-font-size: 16px;
		--header6-font-size: 14px;
	}
}

@media (max-width: 850px) {
	:root {
		--header-size: calc(10px * 5);
		--header-size: calc(var(--grid-size) * 5);
		--content-top-padding: 10px;
		--content-top-padding: var(--grid-size);
		--content-padding: 10px;
		--content-padding: var(--grid-size);
	}
}

.root,
.frameContainer {
	--color-background: rgba(23, 25, 28, 0.4);
	--color-background-faded: rgba(23, 25, 28, 0.64);
	--color-highlight: #ffaf00;
	--color-highlight: var(--color-amber);
	--sd1: rgba(0, 0, 0, 0.2);
  --sd2: rgba(0, 0, 0, 0.14);
  --sd3: rgba(0, 0, 0, 0.12);
}

.root {
	margin: 0;
	background: #17191c;
	background: var(--hero-background);
	width: 100%;
	height: calc(10px * 46);
	height: calc(var(--grid-size) * 46);
	color: #fff;
	color: var(--color-white);
	background-size: cover;
	-webkit-box-pack: center;
	    -ms-flex-pack: center;
	        justify-content: center;
	-webkit-box-align: center;
	    -ms-flex-align: center;
	        align-items: center;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-orient: vertical;
	-webkit-box-direction: normal;
	    -ms-flex-direction: column;
	        flex-direction: column;
}

.svg {
	margin-bottom: 8px;
}

.svg:not(.active) {
	cursor: pointer;
}

.svg path, .svg circle {
	fill: #aaa;
	fill: #aaa;
	fill: var(--font-color-inverse,#aaa);
}

.svg:hover path {
	fill: #fff;
	fill: var(--color-white);
}

.svg.active path,
.svg.active circle {
	fill: #ffaf00;
	fill: var(--color-amber);
}

.headline {
	color: #fff;
	color: var(--color-white);
	width: calc(10px * 60);
	width: calc(var(--grid-size) * 60);
	text-align: center;
	margin-bottom: 0;
	/*margin-bottom: calc(var(--grid-size) * 2);*/
}

.buttonContainer,
.textContainer {
	width: 100%;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-wrap: wrap;
	    flex-wrap: wrap;
	-ms-flex-line-pack: center;
	    align-content: center;
	-webkit-box-pack: space-evenly;
	    -ms-flex-pack: space-evenly;
	        justify-content: space-evenly;

}

.buttonContainer {
	position: relative;
	-webkit-box-align: start;
	    -ms-flex-align: start;
	        align-items: flex-start;
	-webkit-box-pack: center;
	    -ms-flex-pack: center;
	        justify-content: center;
	-webkit-box-flex: 1;
	    -ms-flex-positive: 1;
	        flex-grow: 1;
}

.textContainer {
	-webkit-box-align: end;
	    -ms-flex-align: end;
	        align-items: flex-end;
	padding: calc(10px * 2) 0 calc(10px * 1) 0;
	padding: calc(var(--grid-size) * 2) 0 calc(var(--grid-size) * 1) 0;
	background:	-webkit-gradient(linear, left top, left bottom, color-stop(5%, rgba(0, 0, 0, 0)), to(rgba(0, 0, 0, .95)));
	background:	linear-gradient(180deg, rgba(0, 0, 0, 0) 5%, rgba(0, 0, 0, .95) 100%);
}

.text {
	padding: 0 10px 0 10px;
	padding: 0 var(--grid-size) 0 var(--grid-size);
}

.text p {
	-webkit-hyphens: auto;
	    -ms-hyphens: auto;
	        hyphens: auto;
}

.build,
.select,
.selectDropdown {
	--lg: linear-gradient(110deg, var(--color-from), var(--color-to));
	--lg: linear-gradient(110deg, var(--color-from), var(--color-to));
}

.build,
.select {
	display: -webkit-inline-box;
	display: -ms-inline-flexbox;
	display: inline-flex;
	-webkit-box-align: center;
	    -ms-flex-align: center;
	        align-items: center;
	-webkit-box-pack: center;
	    -ms-flex-pack: center;
	        justify-content: center;
	background: rgba(0, 0, 0, 0);
	color: #fff;
	color: var(--color-white);
	border: 3px solid;
	-o-border-image: var(--lg);
	   border-image: var(--lg);
	border-image: var(--lg);
	border-image-slice: 1;
	margin-bottom: 10px;
	margin-bottom: var(--grid-size);
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	-webkit-user-select: none;
	   -moz-user-select: none;
	    -ms-user-select: none;
	        user-select: none;
}

.build {
	--color-from: rgba(220,0,5,0.24);
	--color-to: rgba(195,12,112,0.24);
	font-size: 0.875em;
	text-transform: uppercase;
	width: auto;
	height: calc(10px * 4);
	height: calc(var(--grid-size) * 4);
	padding-left: 4px;
	padding-right: 4px;
	margin-left: 2px;
	margin-right: 2px;
}

.build:first-child {
	margin-left: 6px;
}

.build:hover {
	--color-from: #dc0005;
	--color-from: var(--color-red);
	--color-to: #c30c70;
	--color-to: var(--color-purple);
	cursor: pointer;
	text-decoration: none;
}

.select {
	-webkit-box-orient: vertical;
	-webkit-box-direction: normal;
	    -ms-flex-direction: column;
	        flex-direction: column;
	-webkit-box-pack: start;
	    -ms-flex-pack: start;
	        justify-content: flex-start;
	width: calc(10px * 24);
	width: calc(var(--grid-size) * 24);
	height: calc(10px * 8);
	height: calc(var(--grid-size) * 8);
}

.select:first-child {
	--color-from: #dc0005;
	--color-from: var(--color-red);
	--color-to: #ffaf00;
	--color-to: var(--color-amber);
	margin-right: calc(10px * 3 + 3px);
	margin-right: calc(var(--grid-size) * 3 + 3px);
}

.select:not(.select:first-child) {
	--color-from: #ffaf00;
	--color-from: var(--color-amber);
	--color-to: #95cc0d;
	--color-to: var(--color-light-green);
}

.select,
.select > label + div {
	height: auto;
	width: 300px !important;
}

.select > label {
	background: rgba(23, 25, 28, 0.3);
	border-bottom: 1px dotted #fff;
	border-bottom: 1px dotted var(--color-white);
	margin-bottom: -2px;
	padding-left: 8px;
	font-weight: bold;
	line-height: 2;
	width: 100%;
}

.selectDropdown {
	padding: 3px;
	background: var(--lg);
	background: var(--lg);
}

.selectDropdown:focus {
	border: none;
	background: var(--lg);
	background: var(--lg);
}

.select button {
	margin-left: 3px;
	width: calc(100% - 6px);
	font-weight: bold;
	border: none;
	color: #ffaf00;
	color: var(--color-amber);
}

.root a {
	text-decoration: none;
}

.root a h3 {
	border-bottom: 1px solid #ff7a00;
	border-bottom: 1px solid var(--color-orange);
}

.btnDown {
	position: absolute;
	display: -webkit-inline-box;
	display: -ms-inline-flexbox;
	display: inline-flex;
	-webkit-box-pack: center;
	    -ms-flex-pack: center;
	        justify-content: center;
	-webkit-box-align: center;
	    -ms-flex-align: center;
	        align-items: center;
	width: 35px;
	height: 35px;
	border-radius: 100%;
	-webkit-box-shadow: 0 0 6px rgba(0,0,0,.12),0 6px 6px rgba(0,0,0,.24);
	        box-shadow: 0 0 6px rgba(0,0,0,.12),0 6px 6px rgba(0,0,0,.24);
	background-color: #ffc334;
}

.btnDown::after {
	position: absolute;
	content: '';
	border: solid black;
  border-width: 0 4px 4px 0;
  display: inline-block;
  padding: 4px;
	-webkit-transform: rotate(45deg);
	        transform: rotate(45deg);
}

.btnDown1 {
	top: 96px;
}

.btnDown2 {
	bottom: 10px;
	right: 28px;
}

.frameContainer {
	position: relative;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-orient: vertical;
	-webkit-box-direction: normal;
	    -ms-flex-direction: column;
	        flex-direction: column;
	-webkit-box-align: center;
	    -ms-flex-align: center;
	        align-items: center;
}

.frameContainer .build:first-child {
	margin-right: 32px;
}

.frameLinks {
	width: 100%;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: distribute;
	    justify-content: space-around;
	max-width: 1020px;
	margin: 8px 0 0 16px;
}

.frameLinks .select {
	display: none;
}

.frame {
	-webkit-box-flex: 1;
	    -ms-flex-positive: 1;
	        flex-grow: 1;
	width: 100%;
	height: 100%;
	max-width: 1020px;
	min-height: calc(100vh - 100px);
	margin: 0 0 10px 16px;
}

.frame.mobile {
	width: calc(100% - 48px);
}

.extLink,
.extLink:visited {
	color: #aaa;
	color: #aaa;
	color: var(--font-color-inverse,#aaa);
	display: -webkit-inline-box;
	display: -ms-inline-flexbox;
	display: inline-flex;
	float: right;
}

.extLink:hover {
	color: #fff;
	color: var(--color-white);
}

.root div[role]>div[aria-selected="false"] {
	line-height: 12px !important;
}

