@media (max-width: 960px) {
	.frameLinks {
		-ms-flex-wrap: wrap;
		    flex-wrap: wrap;
	}
	.build {
		width: calc(10px * 24);
		width: calc(var(--grid-size) * 24);
	}
	.headline {
		font-size: 1.25em;
		width: calc(10px * 36);
		width: calc(var(--grid-size) * 36);
	}
	.btnDown2 {
		bottom: -16px;
		left: 50%;
	}
}

@media (max-width: 768px) {
	.frameLinks>* {
		-webkit-transform: scale(.8);
		        transform: scale(.8);
		-ms-flex-negative: 1;
		    flex-shrink: 1;
		margin: -2px;
	}
	.build { width: auto; }
}

@media (min-width: 768px) {
	.root { margin-top: 56px; }
}

@media (min-width: 1348px) {
	.build {
		width: 100%;
	}
	.build:first-child,
	.build {
		-webkit-box-flex: 1;
		    -ms-flex-positive: 1;
		        flex-grow: 1;
		height: calc(10px * 4);
		height: calc(var(--grid-size) * 4);
		margin-left: 0;
		margin-right: 0;
	}
	.buttonContainer .select {
		display: none;
	}
	.btnDown1 {
		top: 0;
	}
	.frameContainer {
		-webkit-box-align: start;
		    -ms-flex-align: start;
		        align-items: flex-start;
		-webkit-box-orient: horizontal;
		-webkit-box-direction: reverse;
		    -ms-flex-direction: row-reverse;
		        flex-direction: row-reverse;
	}
	.frameContainer .build:first-child {
		margin-right: 0;
	}
	.frameLinks {
		width: 100%;
		display: block;
		margin: 8px auto 0 auto;
		max-width: calc(10px * 30);
		max-width: calc(var(--grid-size) * 30);
	}
	.frameLinks .select {
		display: -webkit-box;
		display: -ms-flexbox;
		display: flex;
		margin-bottom: calc(10px * 2);
		margin-bottom: calc(var(--grid-size) * 2);
	}
}

@media (min-width: 1348px) and (min-height: 720px) {
	.build { height: calc(10px * 5); height: calc(var(--grid-size) * 5) }
}

@media (min-width: 1348px) and (min-height: 800px)  {
	.build { height: calc(10px * 6); height: calc(var(--grid-size) * 6) }
}
