:root {
	/* Color Palette */
	--color-white: #fff;
	--color-off-white: #aaa;
  --color-light: #eee;
	--color-light-gray: #999;
	--color-grey: #a3a3a3;
	--color-dark-gray: #373a3e;
	--color-black: #111;
  --color-dark: #4a00cc;
	--color-red: #dc0005;
  --color-deep-orange: #ff2800;
  --color-orange: #ff7a00;
  --color-amber: #ffaf00;
  --color-yellow: #fadc00;
  --color-lime: #dfdc00;
  --color-light-green: #95cc0d;
  --color-green: #13b20b;
  --color-teal: #339985;
  --color-cyan: #00b1cc;
  --color-light-blue: #6da7d1;
  --color-blue: #0d7ecc;
	--color-dark-blue: #3b4eb8;
  --color-indigo: #3b4eb8;
  --color-deep-purple: #639;
  --color-purple: #c30c70;
  --color-pink: #eb2f59;
  --color-brown: #7a5653;
  --color-blue-grey: #617c8f;
	--color-border: rgba(0, 0, 0, 0.1);

	--font-color: #373a3e;

	--font-color: var(--color-dark-gray);
	--font-color-hover: #111;
	--font-color-hover: var(--color-black);
	--font-color-inverse: #aaa;
	--font-color-inverse: var(--color-off-white);
	--font-color-inverse-hover: #fff;
	--font-color-inverse-hover: var(--color-white);
	--link-color: #ff2800;
	--link-color: var(--color-deep-orange);
	--link-color-hover: #eee;
	--link-color-hover: var(--color-light);

	--dark-background-color: #212329;
	--darker-background-color: #141414;

	/* Typography */
	--body-font-size: 15px;

	--block-quote-font-size: 15px;
	--block-quote-font-weight: 500;

	--pre-font-size: 13px;

	--header1-font-size: 25px;
	--header1-line-height: 1.2;

	--header2-font-size: 21px;
	--header2-line-height: 1.3;

	--header3-font-size: 19px;
	--header3-line-height: 1.4;

	--header4-font-size: 19px;
	--header4-line-height: 1.4;

	--header5-font-size: 17px;
	--header5-line-height: 1.4;

	--header6-font-size: 15px;
	--header6-line-height: 1.4;

	--table-first-column-font-size: 15px;

	--feature-points-font-size: 15px;

	/* App Variables */
	--border-radius: 4px;
	--grid-size: 10px;
	--card-padding: calc(10px * 2);
	--card-padding: calc(var(--grid-size) * 2);

	--header-background-color: #212329;

	--header-background-color: var(--dark-background-color);
	--coloresponsive-header-background-color: #141414;
	--coloresponsive-header-background-color: var(--darker-background-color);
	--hero-background: #17191c;
	--menu-offset: calc(10px * 2);
	--menu-offset: calc(var(--grid-size) * 2);
	--toggle-button-width: calc(10px * 2.6);
	--toggle-button-width: calc(var(--grid-size) * 2.6);
	--header-size: calc(10px * 6);
	--header-size: calc(var(--grid-size) * 6);

	--menu-height: calc(10px * 4.4);

	--menu-height: calc(var(--grid-size) * 4.4);

	--content-top-padding: calc(10px * 6);

	--content-top-padding: calc(var(--grid-size) * 6);
	--content-padding: calc(10px * 3);
	--content-padding: calc(var(--grid-size) * 3);
	--console-background-color: #141414;
	--console-background-color: var(--darker-background-color);
}

@media (max-width: 768px) {
	:root {
		--header1-font-size: 24px;
		--header2-font-size: 22px;
		--header3-font-size: 20px;
		--header4-font-size: 18px;
		--header5-font-size: 16px;
		--header6-font-size: 14px;
	}
}

@media (max-width: 850px) {
	:root {
		--header-size: calc(10px * 5);
		--header-size: calc(var(--grid-size) * 5);
		--content-top-padding: 10px;
		--content-top-padding: var(--grid-size);
		--content-padding: 10px;
		--content-padding: var(--grid-size);
	}
}

.root {
}

.mapImg {
	width: 200px;
	height: auto;
}

.featureOne {
	--link-color: #ffaf00;
	--link-color: var(--color-amber);
	--link-color-hover: #000;
	background: linear-gradient(110deg, #dc0005, #fadc00);
	background: linear-gradient(110deg, var(--color-red), var(--color-yellow));
}

.featureTwo {
	--link-color: #13b20b;
	--link-color: var(--color-green);
	background: linear-gradient(110deg, #fadc00, #13b20b);
	background: linear-gradient(110deg, var(--color-yellow), var(--color-green));
}

.featureThree {
	--link-color: #0d7ecc;
	--link-color: var(--color-blue);
	background: linear-gradient(110deg, #c30c70, #0d7ecc);
	background: linear-gradient(110deg, var(--color-purple), var(--color-blue));
	margin-bottom: calc(10px * -2);
	margin-bottom: calc(var(--grid-size) * -2);
}

.featureBottom {
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-orient: vertical !important;
	-webkit-box-direction: normal !important;
	    -ms-flex-direction: column !important;
	        flex-direction: column !important;
	padding-top: 10px;
	padding-top: var(--grid-size);
	-webkit-box-align: center;
	    -ms-flex-align: center;
	        align-items: center;
	color: #fff;
	background: #212329;
	background: var(--dark-background-color);
}

.btnPlus {
	position: relative;
	top: 10px;
	top: var(--grid-size);
	display: -webkit-inline-box;
	display: -ms-inline-flexbox;
	display: inline-flex;
	-webkit-box-pack: center;
	    -ms-flex-pack: center;
	        justify-content: center;
	-webkit-box-align: center;
	    -ms-flex-align: center;
	        align-items: center;
	width: 54px;
	height: 54px;
	border-radius: 100%;
	-webkit-box-shadow: 0 0 6px rgba(0,0,0,.12),0 6px 6px rgba(0,0,0,.24);
	        box-shadow: 0 0 6px rgba(0,0,0,.12),0 6px 6px rgba(0,0,0,.24);
	background-color: #ffc334;
}

.btnPlus::before,
.btnPlus::after {
	position: absolute;
	content: '';
	height: 4px;
	width: 18px;
	background-color: #2b323a;
}

.btnPlus::after {
	-webkit-transform: rotate(90deg);
	        transform: rotate(90deg);
}

.featureRow {
	-webkit-box-orient: horizontal;
	-webkit-box-direction: normal;
	    -ms-flex-direction: row;
	        flex-direction: row;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
}

.featureRow ul {
	font-size: 15px;
	font-size: var(--feature-points-font-size);
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-orient: vertical;
	-webkit-box-direction: normal;
	    -ms-flex-direction: column;
	        flex-direction: column;
	-webkit-box-pack: center;
	    -ms-flex-pack: center;
	        justify-content: center;
	list-style-type: none;
	margin: 0;
	padding: 0;
}

.featureCardRight {
	width: 45%;
	margin: calc(10px * 10) calc(10px * 20) calc(10px * 10) 0px;
	margin: calc(var(--grid-size) * 10) calc(var(--grid-size) * 20) calc(var(--grid-size) * 10) 0px;
	padding: calc(10px * 3.5);
	padding: calc(var(--grid-size) * 3.5);
}

.featureCardLeft {
	width: 45%;
	margin: calc(10px * 10) 0 calc(10px * 10) calc(10px * 20);
	margin: calc(var(--grid-size) * 10) 0 calc(var(--grid-size) * 10) calc(var(--grid-size) * 20);
	padding: calc(10px * 3.5);
	padding: calc(var(--grid-size) * 3.5);
}

.featureContent {
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	padding: 0;
}

.topFeaturePoint {
	margin-top: 0;
}

.featureTitle {
	color: white;
	width: 45%;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	text-align: center;
	-webkit-box-pack: center;
	    -ms-flex-pack: center;
	        justify-content: center;
	-webkit-box-align: center;
	    -ms-flex-align: center;
	        align-items: center;
	margin: calc(10px * 2.5);
	margin: calc(var(--grid-size) * 2.5);
}

@media (max-width: 960px) {
	.featureRow {
		-webkit-box-orient: vertical;
		-webkit-box-direction: normal;
		    -ms-flex-direction: column;
		        flex-direction: column;
	}
	.featureTitle {
		margin: calc(10px * 5) auto calc(10px * 2.5) auto;
		margin: calc(var(--grid-size) * 5) auto calc(var(--grid-size) * 2.5) auto;
		width: 80%;
	}
	.featureTwo {
		-webkit-box-orient: vertical;
		-webkit-box-direction: reverse;
		    -ms-flex-direction: column-reverse;
		        flex-direction: column-reverse;
	}
	.featureCardLeft {
		height: 100%;
		width: inherit;
		margin: calc(10px * 2.5);
		margin: calc(var(--grid-size) * 2.5);
		margin-bottom: calc(10px * 5);
		margin-bottom: calc(var(--grid-size) * 5);
		max-width: none;
		max-width: initial;
		padding: 0;
	}
	.featureCardRight {
		width: inherit;
		margin: calc(10px * 2.5);
		margin: calc(var(--grid-size) * 2.5);
		height: 100%;
		margin-bottom: calc(10px * 5);
		margin-bottom: calc(var(--grid-size) * 5);
		max-width: none;
		max-width: initial;
		padding: 0;
	}
}
